import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import {
  NgbCarouselModule,
  NgbAlertModule
} from '@ng-bootstrap/ng-bootstrap';
import { NgxMyDatePickerModule } from 'ngx-mydatepicker';
import { ActiveBookingsRoutingModule} from './active-bookings-routing.module';
import { DataTableModule, SharedModule, ButtonModule } from "primeng/primeng";
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PageHeaderModule } from '../../shared';
import { ModalModule} from 'ngx-bootstrap/modal';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { LoadingModule } from 'ngx-loading';
import { ActiveBookingsComponent } from './components/active-bookings/active-bookings.component';
import { BookingTypeComponent } from './components/booking-type/booking-type.component';
import { CreateBookingTypeComponent } from './components/create-booking-type/create-booking-type.component';
import { UpdateBookingTypeComponent } from './components/update-booking-type/update-booking-type.component';
import { ActiveBookingsService } from './services/active-bookings.service';

@NgModule({
  imports: [
    CommonModule,
    NgbCarouselModule.forRoot(),
    NgbAlertModule.forRoot(),
    ActiveBookingsRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    DataTableModule,
    SharedModule,
    NgbModule.forRoot(),
    PageHeaderModule,
    ModalModule.forRoot(),
    ButtonModule,
    LoadingModule,
    NgxMyDatePickerModule.forRoot()
  ],
  declarations: [
    ActiveBookingsComponent, 
    BookingTypeComponent, 
    CreateBookingTypeComponent, 
    UpdateBookingTypeComponent
  ],
  providers: [ActiveBookingsService, NgbModal]
})
export class ActiveBookingsModule { }


