import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Http, HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AppRoutingModule } from './app-routing.module';

import { CustomerSupportModule } from './layout/customer-support/customer-support.module'
import { ActiveBookingsModule } from './layout/active-bookings/active-bookings.module'
import { UserManagementModule } from './layout/user-management/user-management.module'
import { PhotosModule } from './layout/photos/photos.module';

import { AppComponent } from './app.component';
import { AuthGuard, UserService } from './shared';



// AoT requires an exported function for factories
export function HttpLoaderFactory(http: Http) {
//     for development
//     return new TranslateHttpLoader(http, '/start-angular/SB-Admin-BS4-Angular-4/master/dist/assets/i18n/', '.json');
    return new TranslateHttpLoader(http, '/assets/i18n/', '.json');
}
@NgModule({
    declarations: [
        AppComponent
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        FormsModule,
        HttpModule,
        AppRoutingModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: HttpLoaderFactory,
                deps: [Http]
            }
        }),
        CustomerSupportModule,
        ActiveBookingsModule,
        UserManagementModule,
        PhotosModule
    ],
    providers: [AuthGuard, UserService],
    bootstrap: [AppComponent]
})
export class AppModule {
}



