import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ActiveBookingsComponent} from './components/active-bookings/active-bookings.component';
import { BookingTypeComponent} from './components/booking-type/booking-type.component';
import { CreateBookingTypeComponent} from './components/create-booking-type/create-booking-type.component';
import { UpdateBookingTypeComponent} from './components/update-booking-type/update-booking-type.component';
const routes: Routes = [
    { path: '', component:  ActiveBookingsComponent},
    { path: 'booking-type', component: BookingTypeComponent},
    { path: 'create-booking-type', component: CreateBookingTypeComponent},
    { path: 'update-booking-type', component: UpdateBookingTypeComponent}
    
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]  
})
export class ActiveBookingsRoutingModule{}


