import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { routerTransition } from '../../../../router.animations';
import { CustomerSupportService } from '../../../customer-support/services/customer-support.service';
import { LayoutService } from '../../../layout.service';
import { AccountDto, FlightDto } from '../../../customer-support/model/customer-support.model';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-customer-support',
  templateUrl: './customer-support.component.html',
  styleUrls: ['../../../layout.component.scss', './customer-support.component.css'],
  animations: [routerTransition()],
  encapsulation: ViewEncapsulation.None
})
export class CustomerSupportComponent implements OnInit {
  account: AccountDto = new AccountDto;
  // flights: FlightDto[];
  flights: any [] = [];
  accountId: string;
  closeResult: string;
  selectedFlight:any;
  loading: boolean = false;
  errorMessage: string = "Unexpected error encountered.";
  firstName:string;
  lastName:string;
  gender:string;
  birthday:string;
  mobileNumber:string;
  email:string;
  status:string;

  constructor(
    private customerSupportService:CustomerSupportService, 
    private layoutService:LayoutService, 
    private modalService: NgbModal
  ) { }

  ngOnInit() {
    this.flights = [];
  }

  searchAccountDetails() {
    
    this.loading = true;

    this.customerSupportService.getAccountDetails(this.email).subscribe(
      data => {
        if (data.accountId != null) {
          this.account.accountId = data.accountId;
          this.account.firstName = data.firstName;
          this.account.lastName = data.lastName;
          this.account.gender = data.gender;
          this.account.birthday = data.birthday;
          this.account.mobileNumber = data.mobileNumber;
          this.account.email = data.user.email;
          this.account.status = data.user.status;
          this.account.photoUrl = data.user.photoUrl;
          this.account.firebaseId = data.user.firebaseId;
          this.getFlightDetails(this.account.accountId);
        } else {
          this.errMessage("Email does not exist.");
          this.clear();
        }
        
      }, err => {
        console.log(err);
      });
      this.loading = false;
  }

  getFlightDetails(accountId: string) {
    
    this.customerSupportService.getFlightDetails(accountId).subscribe(
      data => {
        if(data.count != 0){
          //console.log(data.items[0].flightId);
          this.flights = data.results;
        }
        
      }, err => {
        console.log(err)
      });
  }

  // setDataToFlights(data: any []) {
  //   for (let obj of data) {
  //     let flight :FlightDto = {
  //       departureDate: obj.flight.departureDate,
  //       airlineName: obj.flight.airline.name,
  //       bookingId: obj.bookingId,
  //       flightNumber: obj.flight.flightNumber
  //     }
  //     this.flights.push(flight);
  //   }
  // }

  open(content) {
    this.modalService.open(content,  {'size': 'lg', backdrop: 'static', keyboard: false}).result.then((result) => {
        this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }
    
  private getDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
            return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
            return 'by clicking on a backdrop';
        } else {
            return  `with: ${reason}`;
        }
    }
/**  
  private getFlightRow(flight:any) {
    this.selectedFlight = flight;
    return false;
  }
*/

  private getFlightDetailsRow(id) {
    this.flights.forEach((item, index) => {
        if(item.id == id){
          this.selectedFlight = this.flights[index];
          return false;
        }
    });
  }

  private cancelBooking(flightBookingId) {
    if(confirm("Are you sure you want to cancel this booking?")){
      this.cancelFLightBooking(flightBookingId);
    }
  }

  private refundBooking(flightBookingId) {
    if(confirm("Are you sure you want to refund this booking?")){
      this.refundFLightBooking(flightBookingId);
    }
  }

  private cancelFLightBooking(flightBookingId){

    this.customerSupportService.cancelBooking(flightBookingId).subscribe(
      data => {
        if(data.result){
          alert('Booking successfully cancelled.');
          this.searchAccountDetails();
        } else {
          this.errMessage(this.errorMessage);
        }
      }, err => {
        console.log(err)
    });

    /**
    this.loading = true;
    let bookingId:string = flight.bookingId;
    let cancellationChargeAmount:number = flight.paymentTransaction.cancellationCharge;
    this.customerSupportService.cancelBooking(bookingId, 0).subscribe(
      resp => {
        this.getFlightDetails(this.account.accountId);
        this.loading = false;
      }, err => {
        this.loading = false;
      }, () => {}
    );
    */
  }

  private refundFLightBooking(flightBookingId){
    
        this.customerSupportService.refundBooking(flightBookingId).subscribe(
          data => {
            if(data.result){
              alert('Booking successfully refunded.');
              this.searchAccountDetails();
            } else {
              this.errMessage(this.errorMessage);
            }
            
          }, err => {
            console.log(err)
        });
    
      }

  clear(): void {
    this.errorMessage = "";
    this.email = "";
    this.account = new AccountDto();
    this.flights = [];
  }

  cancelledButtonDisable(bookingStatus) {
    if (bookingStatus == 'PENDING') {
      return false;
    } else {
      return true;
    }
  }

  private errMessage(msg){
    alert(msg);
    return false;
  }
}





