import { Component, OnInit, ViewEncapsulation, ViewChild, Pipe, PipeTransform, AfterViewInit } from '@angular/core';
import { routerTransition } from '../../../../router.animations';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { ActiveBookingsService } from '../../../active-bookings/services/active-bookings.service';
import { LayoutService } from '../../../layout.service';
import { BookingTypeDto } from '../../../active-bookings/model/booking-type.model';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import {INgxMyDpOptions, IMyDateModel} from 'ngx-mydatepicker';
import { DatePipe, CurrencyPipe } from '@angular/common';
import { LayoutComponent } from '../../../layout.component';

@Component({
  selector: 'app-update-booking-type',
  templateUrl: './update-booking-type.component.html',
  styleUrls: ['../../../layout.component.scss', './update-booking-type.component.css'],
  animations: [routerTransition()],
  encapsulation: ViewEncapsulation.None
})
export class UpdateBookingTypeComponent implements OnInit {
  loading: boolean = false;
  errorMessage: string = "Unexpected error encountered.";
  bookingTypeDetails: BookingTypeDto = new BookingTypeDto;
  bookingTypeId: string = '';
  status: string = ''; 
  serviceType: string = '';
  name: string = '';
  price: number;
  inclusion: string = '';
  addOns: string = '';
  credits: number;
  updatedTime: Date = new Date();
  createdTime: Date = new Date();  
  // dateOfBirthUI: any = [];
  // dateOfBirth: string;

  private alerts: Array<any> = [];

  constructor(
    private activeBookingsService:ActiveBookingsService,
    private layoutService:LayoutService,
    private modalService: NgbModal,
    private activatedRoute: ActivatedRoute, 
    private router: Router) { }

  ngOnInit() {
    this.loading = true;
    this.activatedRoute.queryParams.subscribe((params: Params) => {
      this.bookingTypeId = params['id'];
      this.readBookingType(this.bookingTypeId);
    });

  }

  private myOptions: INgxMyDpOptions = { // other options...
      dateFormat: 'dd/mm/yyyy',
  };

  onDateChanged(event: IMyDateModel): void {
      // on date change
  }

  private readBookingType(selectedId){
    this.activeBookingsService.readBookingType(selectedId).subscribe(
      data => {
        this.serviceType = data.serviceType.toLowerCase();
        this.name = data.name;
        this.price = data.price.toFixed(2);
        this.inclusion = data.inclusion;
        this.addOns = data.addOns;
        this.credits = parseInt(data.freeCredits);
        this.loading = false;
      }, err => {
        console.log(err)
    });

  }

  private updateBookingType(){
    let param = [];
  
    param.push({
      "status":"ACTIVE",
      "serviceType":this.serviceType.toLocaleUpperCase(),
      "name":this.name,
      "price":this.price,
      "inclusion":this.inclusion,
      "addOns":this.addOns,
      freeCredits:this.credits
    });
    this.saveBookingType(param[0]);


  }


  private saveBookingType(bookingTypeDetails){
    this.activeBookingsService.updateBookingType(this.bookingTypeId, bookingTypeDetails).subscribe(
      data => {
        
        if(!this.isEmpty(data)){
          this.successMessage('Booking Type successfully updated.');

          setTimeout(()=>{
            this.closeActiveAlert();
            this.router.navigateByUrl('booking/booking-type');  
          }, 3000);
          
        } else {
          this.errMessage(this.errorMessage);
        }
        
      }, err => {
        console.log(err)
    });
  }

  private errMessage(msg){
    this.closeActiveAlert();
    this.alerts.push({
      id: 1,
      type: 'danger',
      message: msg
    });
    
    return false;
  }

  private successMessage(msg){
    this.closeActiveAlert();
    
    setTimeout(()=>{
      this.alerts.push({
        id: 1,
        type: 'success',
        message: msg
      });
    }, 500);
    
    return true;
  }

  private closeAlert(alert: any) {    
    const index: number = this.alerts.indexOf(alert);
    this.alerts.splice(index, 1);
  }

  private closeActiveAlert(){
    this.alerts.splice(0, 1);
  }

  private isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
  }

}



