import { Component, OnInit, ViewEncapsulation, ViewChild, Pipe, PipeTransform, AfterViewInit } from '@angular/core';
import { routerTransition } from '../../../../router.animations';
import { ActiveBookingsService } from '../../../active-bookings/services/active-bookings.service';
import { LayoutService } from '../../../layout.service';
import { BookingDto } from '../../../active-bookings/model/active-bookings.model';
import { NgbModal, ModalDismissReasons, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import {INgxMyDpOptions, IMyDateModel} from 'ngx-mydatepicker';
import { DatePipe, CurrencyPipe } from '@angular/common';


@Component({
  selector: 'app-booking-type',
  templateUrl: './booking-type.component.html',
  styleUrls: ['../../../layout.component.scss', './booking-type.component.css'],
  animations: [routerTransition()],
  encapsulation: ViewEncapsulation.None
})

export class BookingTypeComponent implements OnInit {
  errorMessage: string = "Unexpected error encountered.";
  loading: boolean = false;
  closeResult: string;
  bookingTypes: any [] = [];
  selectedBookingTypeDetails: any;
  private alerts: Array<any> = [];
  private modalAlerts: Array<any> = [];
  modalReference: NgbModalRef;
  

  constructor(
    private activeBookingsService:ActiveBookingsService, 
    private layoutService:LayoutService, 
    private modalService: NgbModal
  ) { }

  private myOptions: INgxMyDpOptions = { // other options...
      dateFormat: 'dd/mm/yyyy',
  };
  
  ngOnInit() {
    this.getBookingTypes();    
  }
  
  private getBookingTypes(){
    this.loading = true;
    
    this.activeBookingsService.getBookingTypes().subscribe(
      data => {
        this.bookingTypes = data;
        this.loading = false;
        
      }, err => {
        console.log(err)
    });
  }

  
  open(content) {

    this.modalReference = this.modalService.open(content,  {'size': 'lg', backdrop: 'static', keyboard: false});
    
    this.modalReference.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
    
  }

  private getDismissReason(reason: any): string {
      if (reason === ModalDismissReasons.ESC) {
          return 'by pressing ESC';
      } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
          return 'by clicking on a backdrop';
      } else {
          return  `with: ${reason}`;
      }
  }

  private getBookingTypeDetailsRow(id) {

    this.bookingTypes.forEach((item, index) => {
        if(item.bookingTypeId == id){          
          this.selectedBookingTypeDetails = this.bookingTypes[index];
          return false;
        }
    });
  }
  
  private deleteBookingTypeRow(id){
    if(confirm("Are you sure you want to archive this Booking Type?")){
      this.deleteBookingType(id);
    }
  }

  private deleteBookingType(id){
    this.activeBookingsService.deleteBookingType(id).subscribe(
      data => {
        if(!this.isEmpty(data)){
          this.successMessage('Booking Type successfully archived.');
          setTimeout(()=>{
            this.closeActiveAlert();
            this.getBookingTypes();
          }, 3000);          
        } else {
          this.errMessage(this.errorMessage);
        }
        
      }, err => {
        console.log(err)
    });
  }

  private errMessage(msg){
    this.closeActiveAlert();
    this.alerts.push({
      id: 1,
      type: 'danger',
      message: msg
    });
    
    return false;
  }

  private errModalMessage(msg){
    this.closeActiveModalAlert();
      this.modalAlerts.push({
        id: 1,
        type: 'danger',
        message: msg
      });
    
    
    return false;
  }

  private successMessage(msg){
    this.closeActiveAlert();
    
    setTimeout(()=>{
      this.alerts.push({
        id: 1,
        type: 'success',
        message: msg
      });
    }, 500);
    
    return true;
  }

  private successModalMessage(msg){
    this.closeActiveModalAlert();
    
    setTimeout(()=>{
      this.modalAlerts.push({
        id: 1,
        type: 'success',
        message: msg
      });
    }, 500);
    
    return true;
  }

  private closeAlert(alert: any) {    
    const index: number = this.alerts.indexOf(alert);
    this.alerts.splice(index, 1);
  }

  private closeModalAlert(modalAlerts: any) {    
    const index: number = this.modalAlerts.indexOf(alert);
    this.modalAlerts.splice(index, 1);
  }

  private closeActiveAlert(){
    this.alerts.splice(0, 1);
  }

  private closeActiveModalAlert(){
    this.modalAlerts.splice(0, 1);
  }

  private isEmpty(obj) {
    for(var key in obj) {
        if(obj.hasOwnProperty(key))
            return false;
    }
    return true;
  }

}



